<template>
  <div id="add-blog" class="container">
  	<h2>添加博客</h2>
  	<form v-if="!submited">
  		<div  class="form-group">
  			<label for="blog-title">博客标题</label>
  			<input type="text" v-model="blog.title" class="form-control" id="blog-title" placeholder="请输入标题">
  		</div>
  		<div class="form-group">
  			<label for="blog-content">博客内容</label>
  			<textarea class="form-control" v-model="blog.content" rows="4" id="blog-content"></textarea>
  		</div>
  		<div class="checkbox">
  			<label>
  				<input type="checkbox" v-model="blog.categories" value="Vue.js" name="">Vue.js
  			</label>
  			<label>
  				<input type="checkbox" v-model="blog.categories" value="Node.js" name="">Node.js
  			</label>
  			<label>
  				<input type="checkbox" v-model="blog.categories" value="react.js" name="">react.js
  			</label>
  			<label>
  				<input type="checkbox" v-model="blog.categories" value="Angular4" name="">Angular4
  			</label>
  		</div>
  		<select class="form-control" v-model="blog.author">
  			<option v-for="author in authors">{{author}}</option>
  		</select>
  		<button type="submit" class="btn btn-primary" style="margin-top:20px;" v-on:click.prevent="post">添加博客</button>
  	</form>

  	<div v-if="submited">
  		<h3>博客添加成功</h3>
  	</div>

  	<div id="preview">
  		<h3>博客总览</h3>
  		<p>博客标题:{{blog.title}}</p>
  		<p>博客内容:</p>
  		<p class="blog-content">{{blog.content}}</p>
  		<p>博客分类:</p>
  		<ul>
  			<li v-for="category in blog.categories">
  				{{category}}
  			</li>
  		</ul>
  		<p>作者:{{blog.author}}</p>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
      	title:"",
      	content:"",
      	categories:[],
      	author:"xjm"
      },
      authors:["xjm","fch","zlf"],
      submited:false
    }
  },
  methods:{
  	post:function(){
  		this.$http.post("https://jsonplaceholder.typicode.com/posts",{title:this.blog.title,body:this.blog.content,userId:1}).then(function(data){
  			console.log(data);
  			this.submited=true;
  		});
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	#add-blog{
		max-width: 640px;
	}
	#add-blog form{
		max-width: 640px;
		margin:0 auto;
	}
	#preview {
		padding: 10px 20px;
		border: 1px dotted #ccc;
		margin:0 auto;
		margin-top: 20px;
		max-width: 640px;
	}
	#preview p {
		margin-bottom: 0.2rem;
	}
	#preview .blog-content {
		text-indent:2em;
	}
</style>



