<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.body}}</article>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get("https://jsonplaceholder.typicode.com/posts/"+this.id).then(function(data){
				console.log(data);
				this.blog = data.body;
			})
		}
	}
</script>
<style scoped type="text/css">
	#single-blog{
		max-width: 640px;
		margin:0 auto;
		padding: 20px;
		background: #eee;
		border:1px dotted #aaa;
	}
</style>


