<template>
  <div id="show-blogs" v-theme:column="'narrow'">
  	<h2>博客总览</h2>
  	<input type="text" v-model="search" name="" placeholder="搜索">
  	<div v-for="blog in filteredBlogs" class="single-blog">
  		<router-link v-bind:to="'/blog/'+blog.id">
  			<h3 v-rainbow>{{blog.title | to-uppercase}}</h3>
  		</router-link>
  		<article>{{blog.body}}</article>
  	</div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
  	return {
  		blogs:[],
  		search:""
  	}
  },
  created(){
  	this.$http.get("./../static/posts.json").then(
  		function(data){
  			console.log(data);
  			this.blogs = data.body.slice(0,10);
  			console.log(this.blogs);
  		});
  },
  computed:{
  	filteredBlogs:function(){
  		var _this = this;
  		return this.blogs.filter(function(blog){
  			return blog.title.match(_this.search);
  		});
  	}
  },
  filters:{
  	"to-uppercase":function(value){
  		return value.toUpperCase();
  	}
  },
  directives:{
  	'rainbow':{
  		bind(el,binding,vnode){
  			el.style.color = el.style.color = "#" + Math.random().toString(16).slice(2,8);;
  		}
  	}
  }
}
</script>

<style scoped>
#show-blogs{
	max-width: 640px;
	margin:0 auto;
}
#show-blogs input{
	width:100%;
}
#show-blogs .single-blog{
	padding: 20px;
	margin:20px 0;
	box-sizing: border-box;
	background: #ccc;
}
#show-blogs a{
	text-decoration: none;
}
</style>



